/* eslint max-len: 0 */
/* eslint prefer-template: 0 */
/* eslint no-unused-vars: 0 */
/* eslint no-var: 0 */
/* eslint vars-on-top: 0 */
/* eslint prefer-arrow-callback: 0 */
/* eslint func-names: 0 */
/* eslint no-param-reassign: 0 */
/* eslint no-shadow: 0 */
import React, { Component } from 'react';
import C from '../constants';
import { auth, database, storage, storageRef } from '../firebaseApp';


class Article extends Component {
	constructor() {
		super();
		this.submit = this.submit.bind(this);
		this.getImageFilename = this.getImageFilename.bind(this);
		this.getImageURL = this.getImageURL.bind(this);
		this.state = {
			imageFileURL: '',
			imageFileName: ''
		};
	}

	componentDidMount() {
		this.imgKey = this.props.imageKey;
		this.getImageFilename('imageFileName');
	}

	getImageFilename(imageFileName) {
		var value;
		var self = this;
		// console.log('%c The imgKey value of the article is → ' + this.imgKey, 'color: #00aaff; font-weight: bold');
		const imgKey = this.imgKey;
		const imgRef = database.ref('images/' + imgKey + '/');
		imgRef.limitToFirst(1).once('value').then(function takeSnapshot(snapshot, value) {
			snapshot.forEach(function takeChildSnapshot(childSnapshot, value) {
				value = childSnapshot.val().filename;
				// console.log('%c The imageFileName value extracted is → ' + value, 'color: #00aaff; font-weight: bold');
				self.state[imageFileName] = value;
				self.setState(self.state);
				// console.log('Value returned by takeChildSnapshot -> ' + self.state[imageFileName]);
				self.getImageURL('imageFileURL', 'imageFileName');
				return value;
			});
		});
	}

	getImageURL(imageFileURL) {
		// console.log('%c The filename value in firebase storage is → ' + this.state.imageFileName, 'color: #00aaff; font-weight: bold');
		var imageName = this.state.imageFileName;
		if (this.state.imageFileName !== undefined) {
			storageRef.child('images/' + imageName).getDownloadURL().then((url) => {
				this.state[imageFileURL] = url;
				this.setState(this.state);
				// console.log('Given a filename value, this will get the url: ' + url);
			});
		}
	}

	submit(e) {
		e.preventDefault();
		this.props.submitArticleEdit(
			this.refs.article.value,
			this.refs.version.value,
			this.refs.description.value,
			this.refs.homepage.value,
			this.refs.download.value,
			this.refs.imageKey.value
		);
		this.refs.article.value = '';
	}
	render() {
		let button;
		if (this.props.status === C.ARTICLE_EDITING) {
			return (
				<div className="input--wrapper">
					<form onSubmit={this.submit}>
						<input ref="article" defaultValue={this.props.article.content} />
						<input ref="imageKey" defaultValue={this.props.article.imageKey} />
						<input ref="version" defaultValue={this.props.article.version} />
						<input ref="description" defaultValue={this.props.article.description} />
						<input ref="homepage" defaultValue={this.props.article.homepage} />
						<input ref="download" defaultValue={this.props.article.download} />
						<button type="cancel" onClick={this.props.cancelArticleEdit}>✖️</button>
						<button type="submit" onClick={this.submit}>✅</button>
					</form>
				</div>
			);
		}
		if (!this.props.canEdit) {
			button = '';
		} else if (this.props.status === C.ARTICLE_SUBMITTING) {
			button = <button disabled="disabled">Submitting...</button>;
		} else {
			button = (
				<span>
					<button onClick={this.props.startArticleEdit}>📝</button>
					<button onClick={this.props.deleteArticle}>❌</button>
				</span>
			);
		}
		return (
			<div className="cards--wrapper">
				{/* <span><b>{`${this.props.article.username} → `}</b></span> */}
				<span className="card--title__version">version {this.props.article.version}</span>
				<a href={this.props.article.homepage} className="card--title__homepage">{this.props.article.homepage}</a>
				<img style={{ display: this.state.imageFileURL ? 'block' : 'none' }} src={this.state.imageFileURL} alt="plugin_icon" />
				<div className="card--title__name">{this.props.article.content}</div>
				<div className="card--description">{this.props.article.description}</div>
				<div className="card--ctas">
					<a className="card--link__download" href={this.props.article.download}>Download</a>
				</div>
				{button}
			</div>
		);
	}
}

export default Article;



