/* eslint prefer-template: 0 */
/* eslint max-len: 0 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
	submitArticle,
	startArticleEdit,
	cancelArticleEdit,
	submitArticleEdit,
	deleteArticle
} from '../actions/articles';
import Article from './Article';
import { storage } from '../firebaseApp';

class ListOfPlugins extends Component {
	constructor() {
		super();
		this.submitNewArticle = this.submitNewArticle.bind(this);
	}
	submitNewArticle(e) {
		if (!this.props.articles.submitting) {
			e.preventDefault();
			if (this.refs.newArticle.value) {
				this.props.submitArticle(
					this.refs.newArticle.value,
					this.refs.newVersion.value,
					this.refs.newDescription.value,
					this.refs.newHomepage.value,
					this.refs.newDownload.value,
					this.refs.imageKey.value
				);
			}
			// this.refs.newArticle.value = '';
			// this.refs.newVersion.value = '';
		}
	}
	render() {
		let rows = [];
		const testshit = 5485498901206418;
		if (this.props.articles.data) {
			rows = Object.keys(this.props.articles.data).map((qid) => {
				const article = this.props.articles.data[qid];
				const imageKey = this.props.articles.data[qid].imageKey;
				const status = this.props.articles.status[qid];
				/* eslint-disable react/jsx-no-bind */
				const thisStartArticleEdit = this.props.startArticleEdit.bind(this, qid);
				const thisCancelArticleEdit = this.props.cancelArticleEdit.bind(this, qid);
				const thisSubmitArticleEdit = this.props.submitArticleEdit.bind(this, qid);
				const thisDeleteArticle = this.props.deleteArticle.bind(this, qid);
				/* eslint-enable react/jsx-no-bind */
				if (this.props.articles.data[qid].featured) { // get only featured ones (have a featured:true value)
					return (
						<Article
							key={qid}
							qid={qid}
							imageKey={imageKey}
							article={article}
							status={status}
							canEdit={this.props.auth.uid === article.uid}
							startArticleEdit={thisStartArticleEdit}
							cancelArticleEdit={thisCancelArticleEdit}
							submitArticleEdit={thisSubmitArticleEdit}
							deleteArticle={thisDeleteArticle}
						/>
					);
				}
				return Article;
			});
		}
		let content;
		if (this.props.auth.uid) {
			content = (
				<div className="input--wrapper full-width">
					<form onSubmit={this.submitNewArticle}>
						<input ref="newImageKey" placeholder="imageKey" />
						<input ref="newArticle" placeholder="Plugin name" />
						<input ref="newVersion" placeholder="Version" />
						<input ref="newDescription" placeholder="Description" />
						<input ref="newHomepage" placeholder="Homepage" />
						<input ref="newDownload" placeholder="Download URL" />
						<button type="submit" disabled={this.props.articles.submittingNew}>
							{this.props.articles.submittingNew ? 'Submitting...' : 'Submit'}
						</button>
					</form>
				</div>
			);
		} else {
			content = <p>Log in to add a new article of your own!</p>;
		}
		/* this.props.articles.hasReceivedData ? rows : 'Loading articles...' */
		const rowsOrLoading = this.props.articles.hasReceivedData
			? rows
			: 'Loading articles...';
		return (
			<div className="list--container">
				{this.props.articles.errorMessage
					? <p>{this.props.articles.errorMessage}</p>
					: rowsOrLoading
				}
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		articles: state.articles,
		auth: state.auth
	};
};

const mapDispatchToProps = {
	submitArticle,
	startArticleEdit,
	cancelArticleEdit,
	submitArticleEdit,
	deleteArticle,
};

export default connect(mapStateToProps, mapDispatchToProps)(ListOfPlugins);



