/* eslint no-new-func: 0 */
/* eslint max-len: 0 */
/* eslint no-unused-vars: 0 */
/* eslint no-var: 0 */
/* eslint vars-on-top: 0 */
/* eslint prefer-template: 0 */
/* eslint func-names: 0 */
/* eslint prefer-arrow-callback: 0 */

import React, { Component } from 'react';

class HorizontalNavScroll extends Component {
	constructor() {
		super();
		this.hideLeftHandler = this.hideLeftHandler.bind(this);
		this.hideRightHandler = this.hideRightHandler.bind(this);
		this.scrollLeft = this.scrollLeft.bind(this);
		this.scrollRight = this.scrollRight.bind(this);
		this.checkLimits = this.checkLimits.bind(this);
		this.myScrollFunc = this.myScrollFunc.bind(this);
		this.state = {
			scrollLeftValue: 'default',
			currentPosition: 'default',
			nextLeftPosition: 'default',
			nextRightPosition: 'default',
			hideLeftHandler: true,
			hideRightHandler: false,
			hideBothHandlers: false
		};
		window.addEventListener('scroll', this.myScrollFunc);
	}

	componentDidMount() {
		var totalWidth = document.body.scrollWidth;
		var currentScrollfromLeft = (window.pageXOffset !== undefined) ? window.pageXOffset : (document.documentElement || document.body.parentNode || document.body).scrollLeft;
		var doc = document.documentElement;
		var vw = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
		this.state.currentPosition = currentScrollfromLeft;
		var newPositionL = this.state.currentPosition - vw - 10;
		var newPositionR = this.state.currentPosition + vw + 10;
		this.state.nextLeftPosition = newPositionL;
		this.state.nextRightPosition = newPositionR;
		// console.log('didmount--> ' +
		// 	this.state.currentPosition,
		// 	this.state.nextLeftPosition,
		// 	this.state.nextRightPosition
		// );
		this.checkLimits();
	}

	componentDidUpdate() {
		// console.log('didupdate --> ' +
		// 	this.state.currentPosition,
		// 	this.state.nextLeftPosition,
		// 	this.state.nextRightPosition +
		// 	'totalWidth--> ' + document.body.scrollWidth
		// );
	}

	myScrollFunc() {
		var HorizontalNavWrapper = document.getElementById('HorizontalNavWrapper');
		var y = window.pageYOffset;
		if (window.pageYOffset === 0) {
			this.state.hideBothHandlers = false;
			this.setState(this.state);
		} else {
			this.state.hideBothHandlers = true;
			this.setState(this.state);
		}
	}

	hideLeftHandler(hideLeftHandler) {
		setTimeout(function () {
			if (this.state.currentPosition === 0) {
				// console.log('First ;)');
				this.state.hideLeftHandler = true;
				this.setState(this.state);
			} else {
				this.state.hideLeftHandler = false;
				this.setState(this.state);
			}
		}.bind(this), 602);
	}

	hideRightHandler(hideRightHandler) {
		setTimeout(function () {
			if (this.state.nextRightPosition > document.body.scrollWidth) {
				// console.log('Last ;)');
				this.state.hideRightHandler = true;
				this.setState(this.state);
			} else {
				this.state.hideRightHandler = false;
				this.setState(this.state);
			}
		}.bind(this), 602);
	}

	checkLimits() {
		// console.log('checkLimits--> ' +
		// 	this.state.currentPosition,
		// 	this.state.nextLeftPosition,
		// 	this.state.nextRightPosition
		// );
		this.hideLeftHandler();
		this.hideRightHandler();
	}

	scrollRight(nextRightPosition) {
		const jsCode = `
		(function defer() {
				 if (window.jQuery) {
					 $('.move-right-100vw').hide();
					 $('html, body').stop().animate({
						scrollLeft: ` + this.state.nextRightPosition + `
					}, 600);
					setTimeout(function() {
							$('.move-right-100vw').show();
					}, 600);
				 } else {
							setTimeout(function() { defer() }, 50);
				 }
		})();
		`;
		new Function(jsCode)();
		setTimeout(function () {
			var scrollFromLeftValue = (window.pageXOffset || document.documentElement.scrollLeft);
			var vw = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
			this.state.currentPosition = scrollFromLeftValue;
			var newPositionL = this.state.currentPosition - vw - 10;
			var newPositionR = this.state.currentPosition + vw + 10;
			this.state.nextLeftPosition = newPositionL;
			this.state.nextRightPosition = newPositionR;
			this.forceUpdate(self.state);
			this.checkLimits();
			// console.log('scrollRight (after animation has played)--> ' +
			// 	this.state.currentPosition,
			// 	this.state.nextLeftPosition,
			// 	this.state.nextRightPosition
			// );
		}.bind(this), 601);
	}
	scrollLeft(nextLeftPosition) {
		const jsCode = `
		(function defer() {
				 if (window.jQuery) {
					 $('html, body').stop().animate({
						scrollLeft: ` + this.state.nextLeftPosition + `
					}, 600);
				 } else {
							setTimeout(function() { defer() }, 50);
				 }
		})();
		`;
		new Function(jsCode)();
		setTimeout(function () {
			var scrollFromLeftValue = (window.pageXOffset || document.documentElement.scrollLeft);
			var vw = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
			this.state.currentPosition = scrollFromLeftValue;
			var newPositionL = this.state.currentPosition - vw - 10;
			var newPositionR = this.state.currentPosition + vw + 10;
			this.state.nextLeftPosition = newPositionL;
			this.state.nextRightPosition = newPositionR;
			this.forceUpdate(self.state);
			this.checkLimits();
			// console.log('scrollLeft (after animation has played)--> ' +
			// 	this.state.currentPosition,
			// 	this.state.nextLeftPosition,
			// 	this.state.nextRightPosition
			// );
		}.bind(this), 601);
	}

	render() {
		return (
			<div className="horizontal-nav" style={{ display: this.state.hideBothHandlers ? 'none' : 'block' }} id="HorizontalNavWrapper">
				<a className="move-left-100vw" style={{ display: this.state.hideLeftHandler ? 'none' : 'block' }} onClick={this.scrollLeft}>
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="550 287 63 63">
						<g fill="#066CFF" fillRule="evenodd">
							<path d="M578.117 304.57c.586-.586 1.535-.586 2.12 0 .587.586.587 1.535 0 2.12L569.935 317h25.816c.83 0 1.5.672 1.5 1.5s-.67 1.5-1.5 1.5h-25.43l9.922 9.922c.585.586.585 1.535 0 2.12-.294.294-.677.438-1.06.438-.382 0-.765-.144-1.06-.437l-12.674-12.676c-.586-.586-.586-1.535 0-2.12l12.672-12.677z" />
							{/* <path d="M581.5 287c17.074 0 31.5 14.426 31.5 31.5 0 17.074-14.426 31.5-31.5 31.5-17.074 0-31.5-14.426-31.5-31.5 0-17.074 14.426-31.5 31.5-31.5zm0 60c15.45 0 28.5-13.05 28.5-28.5S596.95 290 581.5 290 553 303.05 553 318.5s13.05 28.5 28.5 28.5z" /> */}
						</g>
					</svg>
				</a>
				<a className="move-right-100vw" style={{ display: this.state.hideRightHandler ? 'none' : 'block' }} onClick={this.scrollRight}>
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="462 287 63 63">
						<g fill="#066CFF" fillRule="evenodd">
							<path d="M496.883 304.57c-.586-.586-1.535-.586-2.12 0-.587.586-.587 1.535 0 2.12L505.065 317H479.25c-.828 0-1.5.672-1.5 1.5s.672 1.5 1.5 1.5h25.434l-9.922 9.922c-.586.586-.586 1.535 0 2.12.293.294.676.438 1.058.438.383 0 .766-.144 1.06-.437l12.675-12.676c.586-.586.586-1.535 0-2.12l-12.672-12.677z" />
							{/* <path d="M493.5 287c-17.074 0-31.5 14.426-31.5 31.5 0 17.074 14.426 31.5 31.5 31.5 17.074 0 31.5-14.426 31.5-31.5 0-17.074-14.426-31.5-31.5-31.5zm0 60c-15.45 0-28.5-13.05-28.5-28.5s13.05-28.5 28.5-28.5 28.5 13.05 28.5 28.5-13.05 28.5-28.5 28.5z" /> */}
						</g>
					</svg>
				</a>
			</div>
		);
	}
}

export default HorizontalNavScroll;



