/* eslint no-new-func: 0 */
/* eslint max-len: 0 */
/* eslint no-unused-vars: 0 */
import React, { Component } from 'react';
import List from './List';

class ListOverlay extends Component {
	componentDidMount() {
		const jsCode = `
		(function defer() {
				 if (window.jQuery) {
							 $('.control-search-overlay').click( function(){
								$('body').addClass('search-active');
								$('.control-search-overlay').addClass('search-active');
								$('.input-search').focus();
							});

							$('.icon-close').click( function(){
								$('body').removeClass('search-active');
								$('.control-search-overlay').removeClass('search-active');
							});

							$(window).scroll(function() {
							 if ($(this).scrollTop()>0) {
									$('.control-search-overlay').fadeOut();
							 } else {
								 $('.control-search-overlay').fadeIn();
							 }
							});

				 } else {
							setTimeout(function() { defer() }, 50);
				 }
		})();
		`;
		new Function(jsCode)();
	}

	render() {
		return (
			<div className="search--wrapper">
				<div className="control control-search-overlay">
					<div className="btn-material">
						<svg xmlns="http://www.w3.org/2000/svg" width="30" height="30">
							<path fill="#FFF" fillRule="evenodd" d="M18.175.088c1.58-.002 3.147.314 4.603.93 5.942 2.59 8.692 9.477 6.17 15.448-2.523 5.968-9.38 8.796-15.376 6.34-5.18-2.192-8.098-7.728-6.98-13.24C7.707 4.053 12.55.09 18.174.088zm7.723 8.626c-1.293-3.125-4.342-5.16-7.723-5.16-4.617 0-8.36 3.74-8.36 8.358 0 3.382 2.038 6.43 5.16 7.724 3.125 1.294 6.72.578 9.11-1.812 2.39-2.39 3.107-5.986 1.813-9.11zM.436 27.024l5.7-5.696c.584-.58 1.53-.58 2.114 0l.336.336c.58.584.58 1.53 0 2.114l-5.7 5.697c-.584.58-1.53.58-2.114 0l-.336-.336c-.58-.586-.58-1.53 0-2.116z" />
						</svg>
					</div>
				</div>
				<i className="icon-close material-icons">
					<svg xmlns="http://www.w3.org/2000/svg" width="31" height="31">
						<path fill="#FFF" fillRule="evenodd" d="M25.95.992L15.503 11.438 5.053.992C2.35-1.715-1.71 2.344.994 5.052L11.443 15.5.994 25.945c-2.707 2.707 1.352 6.766 4.06 4.063l10.448-10.446L25.95 30.008c2.706 2.707 6.77-1.356 4.058-4.063L19.558 15.5 30.005 5.055c2.707-2.707-1.356-6.766-4.06-4.063h.005z" />
					</svg>
				</i>
				<div className="search-overlay-content">
					<input className="input-search" placeholder="Search" type="text" />
					/*{ <List /> }*/
				</div>
			</div>
		);
	}
}

export default ListOverlay;



